import Layout from "../../components/Layout"
import IconButton from "../../components/IconButton"
import {IoMdAdd} from "@react-icons/all-files/io/IoMdAdd"
import { useEffect, useReducer } from "react"
import Form from "./Form"
import { useDispatch, useSelector } from "react-redux"
import { get, post } from "../../application/middlewares"
import BgLoader from "../../components/BgLoader"
import toast from "react-hot-toast"
import {BiTrash} from "@react-icons/all-files/bi/BiTrash"

function reducer(state, action){
    console.log(action)
    switch(action.type){
        case "ADD_BANNER":
            return {
                ...state,
                visible:true
            }            
        case "SET_CLOSE_MODAL":
            return {
                ...state,
                visible:false
            }
        case "SET_DATA":
            return {
                ...state,
                data:action.payload
            }
        case "DELETE_IMAGE":
            return {
                ...state,
                data:state.data.filter(item => +item.id !== +action.payload)
            }
        case "SET_UPLOADED_IMAGE":
            return {
                ...state, 
                data:[...state.data, {id:action.payload.image_id, destination:action.payload.destination}],
                visible:false
            }
        default: return {...state}
    }
}

function Banners () {
    const [state, setState] = useReducer(reducer, {
        data:[], visible:false
    })
    const deleteBanner = (id) =>{
        // console.log("Hello wworld")
        dispatch(post({
            url:`api/admin/delete-banner/${id}`,
            token,
            action: (response) =>{
                setState({Type:"DELETE_IMAGE", payload:id})
            }
        }))
    }
    const token = useSelector(state => state.auth.token)
    const dispatch = useDispatch()
    useEffect(()=>{
        dispatch(get({
            url:`api/admin/get-banners`,
            token,
            action: (response) =>{
                if(response.success){
                    setState({type:"SET_DATA", payload:response.data.rows})
                }else{
                    toast.error("Something went wrong")
                }
            }
        }))
    }, [])
    return (
        <Layout header = {<Header handleClick = {()=>setState({type:"ADD_BANNER", payload:""})}/>}>
            <Form
                visible = {state.visible}
                setCloseModal = {()=>setState({type:"SET_CLOSE_MODAL", payload:""})}
                token = {token}
                setUploadedImage = {(value) => setState({type:"SET_UPLOADED_IMAGE", payload:value})}
            />
            <div className = "w-full h-full px-6 overflow-y-auto">
                <div className = "grid grid-cols-3 gap-8 py-10">
                    {state.data?.map(item =>
                        <div  key = {item.id} className = "flex flex-row relative justify-center items-center">
                            <div className = "absolute right-0 top-0 rounded-full">
                                <IconButton tooltip = "Удалить" handleClick = {()=>deleteBanner(item.id)}icon={<BiTrash className="text-2xl "/>}/>
                            </div>
                            <img className = "fill p-2 object-container" src = {`${process.env.REACT_APP_FILE_URL}/${item.destination}-big.webp`}/>
                        </div>
                    )}
                </div>
            </div>
        </Layout>
    )
}

const Header = ({handleClick}) =>(
    <div className="flex flex-row justify-between items-center">
        <IconButton tooltip = "Добавить баннер" handleClick={handleClick} icon={<IoMdAdd className="text-2xl "/>}/>
        {/* <div className="w-80">
            <SearchInput action={(value) => handleSearch(value)} placeholder="Поиск"/>
        </div> */}
    </div>
);
export default Banners